@font-face {
  font-family: "SegoeUIRegular";
  src: url("../fonts/SegoeUI/SegoeUIRegular/SegoeUIRegular.eot");
  src: url("../fonts/SegoeUI/SegoeUIRegular/SegoeUIRegular.eot?#iefix") format("embedded-opentype"), url("../fonts/SegoeUI/SegoeUIRegular/SegoeUIRegular.woff") format("woff"), url("../fonts/SegoeUI/SegoeUIRegular/SegoeUIRegular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

@font-face {
  font-family: "SegoeUIBold";
  src: url("../fonts/SegoeUI/SegoeUIBold/SegoeUIBold.eot");
  src: url("../fonts/SegoeUI/SegoeUIBold/SegoeUIBold.eot?#iefix") format("embedded-opentype"), url("../fonts/SegoeUI/SegoeUIBold/SegoeUIBold.woff") format("woff"), url("../fonts/SegoeUI/SegoeUIBold/SegoeUIBold.ttf") format("truetype");
  font-style: normal;
  font-weight: normal; }

::-webkit-input-placeholder {
  color: #B5B0AE; }

.modal-header {
  background-color: #f6f6f6;
  font-family: SegoeUIRegular; }

.reg-title {
  color: grey; }

.inner-wrap {
  padding: 15px 35px; }

.field {
  font-family: SegoeUIBold;
  padding: 15px 10px;
  margin-top: 15px;
  width: 100%;
  color: #253939;
  border: 1px solid #B5B0AE;
  border-radius: 5px; }

.submit-btn {
  margin-top: 40px;
  border-radius: 5px; }

.separator {
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #B5B0AE;
  line-height: 0.1em;
  margin: 25px 0 20px; }

.interrupt-text {
  font-family: SegoeUIBold;
  background: white;
  color: #B5B0AE;
  padding: 0 10px; }

.google-registr, .facebook-registr {
  display: inline-block;
  width: 32px;
  height: 32px;
  border-radius: 50%; }

.google-registr:hover, .facebook-registr:hover {
  opacity: 60%; }

.google-registr {
  margin-right: 10px;
  background-image: url("../img/google.png"); }

.facebook-registr {
  background-image: url("../img/facebook.png"); }

.recover-pass {
  margin-top: 10px;
  font-family: SegoeUIBold;
  color: #B5B0AE; }

.recover-pass:hover {
  text-decoration: underline; }
